import svg4everybody from 'svg4everybody';
import $ from 'jquery';

$(() => {
	svg4everybody();
});

function textareaResize(){
	var textarea = document.getElementById('textarea');
	var lineHeightVal = $('.underline-text').css('line-height');
	var lineHeight = parseFloat(lineHeightVal, 10);
	var taHeight = textarea.scrollHeight;
	var numberOfLines = Math.floor(taHeight/lineHeight);
	$('.underline-text').css('height', numberOfLines * lineHeight);
}

var sliderElem = document.getElementById('slider');
var thumbElem = sliderElem.children[0];
var positionLeft;
var $thumbElem = $('.skill-range__thumb');

$(function (){
	if ($('.styled').length) {
		$('.styled').styler();
	}

	$('.underline-text').on('input', textareaResize);

});

$(window).on('load', function (){
	const txt = 'В первую очередь вакансия привлекла тем, что имеется возможность работать удаленно.' +
	'Во-вторых, отзывы людей которые с вами работают или работали только положительные. Так же по отзывам сделал вывод,' +
	'что проекты за которые вы беретесь интересные и сложные, что влияет на профессиональный рост.' +
	'Исходя из этого всего: хочется работать и развиваться дальше в вашей команде.';
	$('.underline-text').append(txt);

	textareaResize();

	$('.skill-list__item').on('click', function(){
		$(this).siblings().removeClass('selected');
		$(this).addClass('selected');

		if( $(this).hasClass('skill-list__item_1') ){
			$thumbElem.css('left', '0%');
		}
		if( $(this).hasClass('skill-list__item_2') ){
			$thumbElem.css('left', '19.5%');
		}
		if( $(this).hasClass('skill-list__item_3') ){
			$thumbElem.css('left', '48.5%');
		}
		if( $(this).hasClass('skill-list__item_4') ){
			$thumbElem.css('left', '99.5%');
		}
    })
});

$(window).on('resize', function (){
	textareaResize();
})

    thumbElem.onmousedown = function(e) {
      var thumbCoords = getCoords(thumbElem);
      var shiftX = e.pageX - thumbCoords.left;
      var widthParent = sliderElem.clientWidth;
      var sliderCoords = getCoords(sliderElem);

      document.onmousemove = function(e) {
        var newLeft = e.pageX - shiftX - sliderCoords.left;

        if (newLeft < 0) {
          newLeft = 0;
        }
        var rightEdge = sliderElem.offsetWidth - thumbElem.offsetWidth + 12;
        if (newLeft > rightEdge) {
          newLeft = rightEdge;
        }
        positionLeft = thumbElem.style.left = newLeft/widthParent*100;

        thumbElem.style.left = positionLeft + '%';
      }

      document.onmouseup = function() {
        document.onmousemove = document.onmouseup = null;
        
        if( positionLeft > 19.5 && positionLeft < 48.5 ){
        	thumbElem.style.left = 19.5 + '%';
        	$('.skill-list__item').siblings().removeClass('selected')
        	$('.skill-list__item_2').addClass('selected');
        } else {
        	if( positionLeft > 48.5 && positionLeft < 73 ){
        		thumbElem.style.left = 48.5 + '%';
        		$('.skill-list__item').siblings().removeClass('selected')
        		$('.skill-list__item_3').addClass('selected');
        	} else {
        		if( positionLeft > 73){
        			thumbElem.style.left = 99.5 + '%';
        			$('.skill-list__item').siblings().removeClass('selected')
        			$('.skill-list__item_4').addClass('selected');
        		} else{
        			thumbElem.style.left = 0 + '%';
        			$('.skill-list__item').siblings().removeClass('selected')
        			$('.skill-list__item_1').addClass('selected');
        		}	
        	}
        }
      };

      return false;
    };


    thumbElem.ondragstart = function() {
      return false;
    };

    function getCoords(elem) {
      var box = elem.getBoundingClientRect();

      return {
        left: box.left + pageXOffset
      };
    }


